import Endpoint from '@/services/Endpoint'

export default {
  reports (payload) {
    return Endpoint().get('/s1/report/report', {
      params: payload
    })
  },
  createReport (payload) {
    return Endpoint().post('/s1/report/report', payload)
  },
  modifyReport (payload) {
    return Endpoint().patch('/s1/report/report', payload)
  },
  reportParameters (payload) {
    return Endpoint().get(`/s1/report/report/${payload.reportId}/parameter`, {
      params: payload
    })
  },
  createReportParameter (payload) {
    return Endpoint().post(`/s1/report/report/${payload.reportId}/parameter`, payload)
  },
  modifyReportParameter (payload) {
    return Endpoint().patch(`/s1/report/report/${payload.reportId}/parameter`, payload)
  },
  reportPrices (payload) {
    return Endpoint().get(`/s1/report/report/${payload.reportId}/price`, {
      params: payload
    })
  },
  createReportPrice (payload) {
    return Endpoint().post(`/s1/report/report/${payload.reportId}/price`, payload)
  },
  modifyReportPrice (payload) {
    return Endpoint().patch(`/s1/report/report/${payload.reportId}/price`, payload)
  },
  expireReportPrice (payload) {
    return Endpoint().delete(`/s1/report/report/${payload.reportId}/price`, {
      params: payload
    })
  },
  reportApps (payload) {
    return Endpoint().get('/s1/report/report/app', {
      params: payload
    })
  },
  createReportApp (payload) {
    return Endpoint().post('/s1/report/report/app', payload)
  },
  deleteReportApp (payload) {
    return Endpoint().delete('/s1/report/report/app', {
      params: payload
    })
  },
  runReport (payload) {
    return Endpoint().post(`/s1/report/report/${payload.reportId}/run`, payload)
  },
  reportWorkflowInstances (payload) {
    return Endpoint().get('/s1/report/report/workflow/instance', {
      params: payload
    })
  }
}
