import ReportService from '@/services/report/ReportService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let ReportDefinitionTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultReportTableFilter: {
      type: String,
      default: null
    },
    defaultReportTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      reportTableData: [],
      reportTableFilterTimer: null,
      reportTableFilter: this.defaultReportTableFilter,
      reportTableOrderBy: this.defaultReportTableOrderBy,
      reportTableContainer: 'loading-container',
      reportTableBusy: false,
      reportTableTotalRows: 0,
      reportTableCurrentPage: 1,
      reportTablePerPage: 10
    }
  },
  computed: {
    reportTableNoDataText: function () {
      return this.reportTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    reportTableTotalPages: function () {
      return Math.ceil(this.reportTableTotalRows / this.reportTablePerPage)
    },
    reportTablePagingInfo: function () {
      return this.pagingInfo(this.reportTableCurrentPage, this.reportTablePerPage, this.reportTableTotalRows)
    }
  },
  methods: {
    loadReportTableData (loadingContainerId) {
      let self = this
      this.reportTableBusy = true
      this.reportTableContainer = loadingContainerId || this.reportTableContainer
      this.startLoading(this.reportTableContainer)
      ReportService.reports({
        pageIndex: this.reportTableCurrentPage - 1,
        pageSize: this.reportTablePerPage,
        filter: this.reportTableFilter,
        orderByField: this.reportTableOrderBy,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.reportTableContainer)
        self.reportTableBusy = false
        self.reportTableTotalRows = response.data.totalRows
        self.reportTableData = response.data.reportList || []
      }).catch(function (error) {
        self.stopLoading(self.reportTableContainer)
        self.reportTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshReportTable () {
      this.loadReportTableData()
    },
    filterReportTable (filter) {
      let self = this
      if (this.reportTableFilter !== filter) {
        clearTimeout(this.reportTableFilterTimer)
        this.reportTableFilterTimer = setTimeout(() => {
          self.reportTableFilter = filter
          self.loadReportTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.reportTableFilterTimer)
      }
    },
    orderReportTable (orderBy, active) {
      this.reportTableOrderBy = (active ? '' : '-') + orderBy
      this.loadReportTableData()
    }
  },
  watch: {
    reportTableCurrentPage: function () {
      this.refreshReportTable()
    },
    reportTablePerPage: function () {
      this.refreshReportTable()
    }
  }
}

export default ReportDefinitionTableMixin
